import zaha1 from './i/zaha-2.png'
import zaha2 from './i/zaha-1.png'
import biarke1 from './i/biarke-1.png'
import biarke2 from './i/biarke-2.png'
import aalto1 from './i/aalto-1.png'
import aalto2 from './i/aalto-2.png'
import zumtor1 from './i/zumtor-1.png'
import zumtor2 from './i/zumtor-2.png'
import s from './index.css';

const zahaImgs = [zaha1, zaha2]
const biarkeImgs = [biarke1, biarke2]
const aaltoImgs = [aalto1, aalto2]
const zumtorImgs = [zumtor1, zumtor2]

const root = document.getElementById('root')
root.className = s.root

const size = window.innerWidth > 400 ? 100 : 15
let winWidth
let winHeight
let blockXCount
let blockYCount
let blockTotalCount
let blocks

function recalcSizes() {
  winWidth = window.innerWidth
  winHeight = window.innerHeight
  blockXCount = Math.ceil(winWidth / size)
  blockYCount = Math.ceil(winHeight / size)
  blockTotalCount = blockXCount * blockYCount

  blocks = createBuilding(zaha1)
  root.innerHTML = ''
  blocks.forEach(block => root.appendChild(block))

}
setTimeout(() => {

}, 1000)
recalcSizes()

if (window.innerWidth > 400) {
  window.addEventListener('resize', () => recalcSizes(), false)
}

function createBuilding(imgSrc: string, offsetX = 0) {
  const blocks = []
  for (let y = 0; y < blockYCount; y++) {
    for (let x = 0; x < blockXCount; x++) {
      const xPos = x * size + offsetX * (size+2)
      const yPos = y * size
      blocks.push(createBlock(size, imgSrc, xPos, yPos, false))
    }
  }
  return blocks
}

function createBlock(size: number, imgSrc: string, x: number, y: number, isEmpty: boolean) {
  const img = document.createElement('img')
  img.className = s.img + (isEmpty ? ` ${s.empty}` : '')
  img.style.left = `${x}px`
  img.style.top = `${y}px`
  img.style.width = `${size}px`
  img.style.height = `${size}px`
  img.style.objectPosition = `${randObjPos()}% ${randObjPos()}%`
  img.src = [zaha1, zaha2][getRandomInt(0, 1)]
  return img
}

function createGroup() {
  const g = document.createElement('div')
  g.className = s.group
  return g
}

let i = window.pageYOffset;
let prevScrollY = window.pageYOffset
setTimeout(() => {
  window.addEventListener('scroll', () => {
    const pageYOffset = window.pageYOffset
    const scrollPerc = _get_scroll_percentage()
    let inc = 1
    if (prevScrollY > pageYOffset) {
      inc = -1
    }
    prevScrollY = pageYOffset
    for (let j = 0; j < blockXCount; j++) {
      const block = blocks[i % blockTotalCount]
      i += inc
      block.style.objectPosition = `${randObjPos()}% ${randObjPos()}%`
      block.src = getRandImgByScrollPos(scrollPerc)
    }
  }, {passive: true})
}, 1000)


function getRandImgByScrollPos(scrollPerc: number): string {
  // pageYOffset -- ?
  // scrollHeight -- 100
  //
  // ? = (pageYOffset / scrollHeight) * 100
  const currPerc = scrollPerc
  let imgs = zahaImgs
  if (currPerc < 10) {
    imgs = zahaImgs
  } else if (currPerc < 20) {
    imgs = zahaImgs.concat(biarkeImgs)
  } else if (currPerc < 30) {
    imgs = biarkeImgs
  } else if (currPerc < 40) {
    imgs = biarkeImgs.concat(aaltoImgs)
  }
  else if (currPerc < 50) {
    imgs = aaltoImgs
  } else if (currPerc < 60) {
    imgs = aaltoImgs.concat(zumtorImgs)
  }
  else if (currPerc < 70) {
    imgs = zumtorImgs
  }
  else if (currPerc < 80) {
    imgs = zumtorImgs.concat(biarkeImgs).concat(aaltoImgs)
  }
  else {
    imgs = zumtorImgs.concat(biarkeImgs).concat(aaltoImgs).concat(zahaImgs)
  }
  return imgs[getRandomInt(0, imgs.length - 1)]
}

function randObjPos() {
  return getRandomInt(0, 80)
}

function getRandomInt(min, max) {
  // случайное число от min до (max+1)
  let rand = min + Math.random() * (max + 1 - min);
  return Math.floor(rand);
}





/**
 * Get current browser viewpane heigtht
 */
function _get_window_height() {
  return window.innerHeight ||
    document.documentElement.clientHeight ||
    document.body.clientHeight || 0;
}

/**
 * Get current absolute window scroll position
 */
function _get_window_Yscroll() {
  return window.pageYOffset ||
    document.body.scrollTop ||
    document.documentElement.scrollTop || 0;
}

/**
 * Get current absolute document height
 */
function _get_doc_height() {
  return Math.max(
    document.body.scrollHeight || 0,
    document.documentElement.scrollHeight || 0,
    document.body.offsetHeight || 0,
    document.documentElement.offsetHeight || 0,
    document.body.clientHeight || 0,
    document.documentElement.clientHeight || 0
  );
}


/**
 * Get current vertical scroll percentage
 */
function _get_scroll_percentage() {
  return (
    (_get_window_Yscroll() + _get_window_height()) / _get_doc_height()
  ) * 100;
}
